import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { fetchArticles } from '../../actions';

class ArticleList extends React.Component {
  componentDidMount() {
    this.props.fetchArticles();
  }

  renderAdmin(Article) {
    if (Article.userId === this.props.currentUserId) {
      return (
        <div className="right floated content">
          <Link to={`/Articles/edit/${Article.id}`} className="ui button primary">
            Edit
          </Link>
          <button className="ui button negative">Delete</button>
        </div>
      );
    }
  }

  renderList() {
    return(  this.props.Articles.length > 0 ?
      this.props.Articles[0].map(Article => {
        return (
          <div className="item" key={Article.id}>
            {this.renderAdmin(Article)}
            <i className="large middle aligned icon camera" />
            <div className="content">
              {Article.title}
              <div className="description">{Article.description}</div>
            </div>
          </div>
        );
      }) : null)
    
   
  }

  renderCreate() {
    if (this.props.isSignedIn) {
      return (
        <div style={{ textAlign: 'right' }}>
          <Link to="/Articles/new" className="ui button primary">
            Create Article
          </Link>
        </div>
      );
    }
  }

  render() {
    return (
      <div>
        <h2>Articles</h2>
        <div className="ui celled list">{this.renderList()}</div>
        {this.renderCreate()}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    Articles:  Object.values(state.article),
    currentUserId: state.auth.userId,
    isSignedIn: state.auth.isSignedIn
  };
};

export default connect(
  mapStateToProps,
  { fetchArticles }
)(ArticleList);
